<template>
  <Layout>
    <PageHeader :title="title" :items="items" />

    <div class="row">
    <div class="col-3">
        <div class="form-group">
            <label for="exampleFormControlSelect1" style="font-size:14">Técnicos pendientes por ajuste {{tecnicos.length}}</label>
            <select class="form-control" v-model="tecnico_id" @change="listarPendientes()" id="exampleFormControlSelect1">
                <option :value="tecnico.id" v-for="tecnico in tecnicos" :key="tecnico.id">{{tecnico.nombre}} {{tecnico.apellido}} {{tecnico.cdaaths.length}} ajustes pendientes</option>
            </select>
        </div>
    </div>
    <div  v-if="cuentas.length>0" class="col-3 py-4" style="margin-top:5px">
        <div class="">
            <b-button class="float-left btn-success" left @click="$bvModal.show('modal');editMode=false;ver=false;">Listar </b-button>
        </div>
    </div>
    <div v-else class="col-3 py-4" style="margin-top:5px">
        <div class="">
            <b-button class="float-left btn-success" left @click="listarPendientes()">Buscar</b-button>
        </div>
    </div>
      <div class="col-12">
        <div class="card">
          <div class="card-body">
            <h4 class="card-title"></h4>
            <div class="row mt-4">
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_length" class="dataTables_length">
                  <label class="d-inline-flex align-items-center">
                    Show&nbsp;
                    <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
                  </label>
                </div>
              </div>

              <!-- Search -->
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_filter" class="dataTables_filter text-md-right">
                  <label class="d-inline-flex align-items-center">
                    Search:
                    <b-form-input
                      v-model="filter"
                      type="search"
                      placeholder="Search..."
                      class="form-control form-control-sm ml-2"
                    ></b-form-input>
                  </label>
                </div>
              </div>
              <!-- End search -->
            </div>
            <!-- Table -->
            <div class="table-responsive mb-0">
              <b-table
                :items="formatos"
                :fields="fields"
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
              <template v-slot:cell(status)="data">
                  <b-badge variant="warning" v-if="data.item.status==='Pendiente'">{{data.item.status}}</b-badge>
                  <b-badge variant="info" v-if="data.item.status==='Archivado'">{{data.item.status}}</b-badge>
              </template>
             <template v-slot:cell(tecnico)="data">
                <b> {{data.item.Tecnico_atha.nombre}} {{data.item.Tecnico_atha.apellido}}</b>
              </template>
              <template v-slot:cell(creado)="data">
                <b> {{ data.item.created_at | fecha}}</b>
              </template>
              <template v-slot:cell(total)="data">
                <b> {{parseFloat(data.item.total) | moneda}}</b>
              </template>
                <template v-slot:cell(actions)="data">
                <b-dropdown size="sm" class="">
                  <template v-slot:button-content>
                    Action
                    <i class="mdi mdi-chevron-down"></i>
                  </template>
                    <b-dropdown-item-button @click="editMode=false;ver=true;setear(data.item.id)"> Ver </b-dropdown-item-button>
                    <b-dropdown-item-button @click="editMode=false;ver=true;ArchivarCuestion(data.item.id)"> Archivar</b-dropdown-item-button>
                </b-dropdown>
                </template>
              </b-table>
            </div>
            <div class="row">
              <div class="col">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>



    </div>

    <b-modal id="modal" false size="lg"   title="Formato de ajuste" hide-footer>

          <b-card class="text-left" v-for="item in form.items" :key="item.id">
            <b-card-title>
              <h2 class="card-title lead">CONSECUTIVO <span>ATH-{{item.consecutivo}}</span></h2>
            </b-card-title>
            <b-card-title>
              <h6 class="card-title">{{item.tipo_llamada}}-{{item.llamada}}</h6>
            </b-card-title>
              <h5 class="text-right text-info"><span>{{item.excedente | moneda}}</span></h5>
          </b-card>
        
          <h4 class="text-right text-success">Total: <span>{{form.total | moneda}}</span></h4>
          <button class="btn btn-block float-right btn-success" @click="switchLoc" v-if="!ver && !editMode">Crear Formato</button>

   
    </b-modal>


  </Layout>
</template>

<script>
import moment from 'moment';   
import VueHtml2pdf from 'vue-html2pdf';
import vue2Dropzone from "vue2-dropzone";
import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";


/**
 * Dashboard component
 */
export default {
  components: {
    vueDropzone: vue2Dropzone,
    Layout,
    PageHeader,
    ValidationProvider,
    ValidationObserver,
    VueHtml2pdf
  },
  data() {
    return {
      title: "Administracion",
      items: [
        {
          text: "Gestión corporativa"
        },
        {
          text: "formatos",
          active: true
        }
      ],
     dropzoneOptions: {
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        headers: { "My-Awesome-Header": "header value" }
      },
      tecnico_id:"",
      ver:false,
      url:"",
      url_firma:"",
      modal: true,
      file:null,
      firma:null,
      email: "",
      password: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortBy: "age",
      sortDesc: false,
      fields: ["status","creado","tecnico","total","actions"],
      formatos: [],
      abonados: [],
      cuentas: [], 
      tecnicos: [], 
      consecutivos: [],
       json_fields: {
            'Item': 'id',
            'Descripcion': 'descripcion',
            'Valor unitario': 'precio',
            'Medida' :'medida',
            'Cantidad' :'cantidad',
            'Total' :'total',
      }, 
      editMode:false,
      form:{
          'id':'',
          'cargo':'',
          'descripcion':'',
          items:[]
      }
    }
  },

     filters: {
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 1
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        },
        fecha: function (value) {
         moment.locale('es');
        let fechafina =moment(value).format('MMMM DD YYYY')
           return fechafina
        }
    },
  created(){
    this.listarUsers();
  },
  methods: {

    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }, 
    ...mapActions(['guardarUsuario']),
    switchLoc(){
       this.$swal({
         title: 'Desea agregar este formato?',
         icon: 'question',
         iconHtml: '',
         confirmButtonText: 'Si',
         cancelButtonText: 'No',
         showCancelButton: true,
         showCloseButton: true
       }).then((result) => {
         if (result.isConfirmed) {
            this.agregarFormato();
         }
       })
    },
    generateReport () {
        this.$refs.html2Pdf.generatePdf()
     },
   cargar(index){
    for (let index = 0; index < this.consecutivos.length; index++) {
        if (this.consecutivos.id===index) {
         this.form.items.push({
            id:this.consecutivos.id,
            valor_analista:this.consecutivos.total_tecnico,
            valor_ajuste:this.consecutivos.total_tecnico,
            precio:this.consecutivos.precio,
            medida:this.consecutivos.medida,
            cantidad:this.consecutivos.cantidad,
            total:this.consecutivos.total,

          });
        }
        const element = array[index];
        
    }

     this.totalSac();
    },
    eliminarItem(index){
       this.form.items.splice(index, 1);  
       this.totalSac();
    },
   async agregarFormato(){
     let data = new FormData();
      var formulario = this.form;
        for ( var key in formulario) {
            if (key=='items') {
                data.append(key,JSON.stringify(formulario[key]));
            }else{
                data.append(key,formulario[key]);
            }
        }
       await this.axios.post('api/ath/formatodeajuste', data, {
           headers: {
            'Content-Type': 'multipart/form-data'
           }}).then(response => {
            if (response.status==200) {
               this.$swal(
                   'Agregado con exito!',
                    '',
                    'success');
              this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               this.listarformatos();
               this.listarTecnicos();
  
              }
            }).catch(e => {
              console.log(e.response.data.menssage);
              this.$swal(e.response.data);
          });
      },
    async editarformatos(){
     let data = new FormData();
       var formulario = this.form;
        for (var key in formulario) {
          data.append(key,formulario[key]);
        }
        await this.axios.put('api/formatos', data).then(response => {
            if (response.status==200) {
               this.$swal('Editado con exito','','success');
               this.listarformatos();
               this.listarPendientes();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
                this.$swal('ocurrio un problema','','warning');
            });
     },
     async archivarFormato(id){
        let data = new FormData();
        data.append('id',id);
        await this.axios.post('api/ath/formatodeajuste/archivar',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                  this.listarFormatos();
                    this.$swal(
                        'Archivado con exito!',
                        '',
                        'success'
                    );
                }
              }).catch(e => {
                this.$swal(
                    'No se pudo realizar esta accion !',
                      '',
                      'warning'
                );
          });
      }, 
      ArchivarCuestion(id){
        this.$swal({
          title: 'Desea archivar este formato?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.archivarFormato(id);
          }
        })
      },
      resete(){
        var formulario = this.form;
        for (var key in formulario) {
             this.form[key]="";
       }
      },
      setear(id) {
        for (let index = 0; index < this.formatos.length; index++) {
          if (this.formatos[index].id===id) {
            this.form.id=this.formatos[index].id;
            this.form.items=JSON.parse(this.formatos[index].items);
            this.form.total=this.formatos[index].total;
            this.$root.$emit("bv::show::modal", "modal", "#btnShow");
            return;
          }
        }
      },
    async  listarPendientes(){
    let data = new FormData();
    data.append("id",this.tecnico_id);
       await this.axios.post('api/ath/cuentasdeajuste',data)
       .then((response) => {
          this.cuentas = response.data;
          console.log(this.cuentas);
          if (this.cuentas.length>0) {
            this.form.tecnico_id=this.cuentas[0].tecnico_id;
            this.form.coordinador_id=this.cuentas[0].programacion_ath.coordinador_id;
            this.form.analista_id=this.cuentas[0].programacion_ath.analista_id;
            this.form.items=[];
            for (let index = 0; index < this.cuentas.length; index++) {
              this.form.items.push({
                consecutivo:this.cuentas[index].id_programacion,
                tipo_llamada:this.cuentas[index].programacion_ath.tipo_llamada,
                llamada:this.cuentas[index].programacion_ath.llamada,
                descripcion:this.cuentas[index].programacion_ath.gestion_aths[0].descripcion, 
                excedente:this.cuentas[index].programacion_ath.legalizaciones_aths[0].excedente, 
                transporte:0,
                fecha:this.cuentas[index].programacion_ath.gestion_aths[0].created_at, 
                reclamo:false,
                descripcion_reclamo:"",          
              });
            }
          }
          this.form.total=0;
          for (let index = 0; index < this.form.items.length; index++) {
            this.form.total= parseFloat(this.form.items[index].excedente)+parseFloat(this.form.total);            
          }
        })
        .catch((e)=>{
          console.log('error' + e);
        })
      },
      async listarFormatos(){
       await this.axios.get('api/ath/formatodeajustes')
        .then((response) => {
          this.formatos = response.data.rows;
        })
        .catch((e)=>{
          console.log('error' + e);
        })
      },
      async  listarTecnicos(){
       await this.axios.get('api/ath/cuentasdeajuste/tecnicos')
        .then((response) => {
          this.tecnicos = response.data;
        })
        .catch((e)=>{
          console.log('error' + e);
        })
      },
      setEmail(){
        this.form.username=this.form.email;
        console.log("holas");
      },
      setRoles(){
        if (this.form.tipo==="Administrador") {
            this.form.codigo="";
            this.form.entidad="No";
            this.form.cargo=""
            this.form.roles="3"
        } else if(this.form.tipo==="Coordinador") {
            this.form.entidad="No";
            this.form.cargo="";
            this.form.roles="2"
        }else{
            this.form.roles="1"
        }
      },
      onFileChange(e) {
        const file = e.target.files[0];
        this.url = URL.createObjectURL(file);
      },
      toggleModal () {
        this.modal = !this.modal
      },
      session(){
        if (localStorage.getItem('token')) {
          const token=localStorage.getItem('token');
          this.guardarUsuario(token);
          }else{
          this.$router.push({ name: 'Home' });
          }
        }
  },
    created(){
        this.session();
        this.listarFormatos();
        this.listarTecnicos();

      },
     mounted() {

    },
    computed: {
      ...mapState(['usuarioDB']),
    rows() {
      return this.formatos.length;
    },
  },
}
</script>
